* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #f1f5f8;
  color: #222;
}

.booklist {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
}

.book {
  background-color: #fff;
  border-radius: 1rem;
  padding: 1rem 2rem;
  display: grid;
  text-align: center;
}

.book-img {
  width: auto;
  height: 200px;
}

.book h1 {
  margin-top: 0.25rem;
}

.book h4 {
  color: #617d98;
  font-size: 0.75rem;
  margin-top: 0.25rem;
}
@media screen and (min-wdith: 768px) {
  .booklist {
    grid-template-columns: repeat(3, 1fr);
  }
}
